html{
  overflow-x: hidden !important;
}
body{
  font-family: 'Montserrat', sans-serif !important;
  overflow-x: hidden !important;
}
.shadow{
  box-shadow: 0px 2px 5px 1px #dedede !important;
  padding-top: 15px;
}
canvas.chartjs-render-monitor:nth-child(5){
  margin-left: -3px !important;
}
@media(max-width:767px){
  .dashboard-img{
    filter: blur(3px);
    -webkit-filter: blur(3px);
    transform: scale(2);
    margin-top: 30px;
    overflow: hidden !important;
  }
  h2{
    text-transform: uppercase;
    font-size: 20px;
    font-weight: 800;
  }
  .container{
    padding: 0 !important;
  }
  .sub-heading{
    font-size: 22px;
    z-index: 9;
    margin-top: 25px;
    font-weight: 900;
    text-align: center;
    color: #fff;
    text-shadow: 1px 2px 11px black
  }
  .btn-rct{
    text-align: center;
  }
  .mt-100{
    margin-top: 100px;
  }
  .mx-4{
    margin-left: 0 !important;
    margin-right: 0 !important;
  }
  .sub-heading-1-main{
    font-size: 23px;
    font-weight: 900;
    line-height: 1.5;
  }
  p{
    font-size: 12px;
  }
  .fa{
    font-size: 50px;
  }
  .text-font{
    font-size: 25px;
  }
}
@media (min-width: 768px){
  h2{
    text-transform: uppercase;
    font-size: 20px;
    font-weight: 800;
  }
  p{
    font-size: 12px;
  }
  .sub-heading{
    font-size: 45px;
    z-index: 9;
    margin-top: 150px;
    font-weight: 900;
    text-align: center;
    color: #fff;
    text-shadow: 1px 2px 11px black
  }
  .btn-rct{
    position: absolute;
    text-align: center;
    bottom: 200px;
    z-index: 10;
  }
  .left-btn{
    right: 8px;
  }
  .right-btn{
    left: 25px;
  }
  .sub-heading-1-main{
    font-size: 33px;
    font-weight: 900;
    line-height: 1.5;
  }
  .fa{
    font-size: 75px;
  }
  .d-gap{
    margin-top: 50px;
  }
  .text-font{
    font-size: 40px;
  }
}
.dashboard-img{
  filter: blur(3px);
  -webkit-filter: blur(3px);
}
.fw-8{
  font-weight: 800;
}
.fw-7{
  font-weight: 700;
}
.fw-6{
  font-weight: 600;
}
.note{
  text-align: center;
  margin: auto;
}
.btn{
  width: 170px;
}
.sub-heading-1{
  font-size: 20px;
  font-weight: 900;
  color: #000;
  line-height: 1.5;
}
.m-0{
  margin: 0 !important;
}
.btn:hover{
  box-shadow: 2px 50px 40px -10px #000000c4;
  transition: all 0.7s ease-in-out;
}
.btn{
  transition: all 0.5s ease-in-out;
  box-shadow: 2px 10px 40px -10px #000000c4;
}
a{
  color: inherit;
}
a:hover{
  color: inherit; 
}
.ScrollUpButton__Container{
  background: #1ea076 !important;
border-radius: 50%;
z-index: 120;
}
.mt-100{
  margin-top: 150px;
  margin-bottom: 150px;
}